<template>
    <div>
        <v-breadcrumbs :items="items">
            <template v-slot:divider>
                <v-icon>mdi-chevron-right</v-icon>
            </template>
        </v-breadcrumbs>
        <v-layout wrap>
            <v-flex xs12 sm12 md4 lg4>
                <div class="mx-8 my-8">
                    <h2></h2>
                </div>
            </v-flex>
            <v-flex xs12 sm12 md8 lg8>
                <v-card  class="text-center mx-5 my-5">
                    <h3>ແກ້ໄຂໂປຣຟຣາຍ</h3>
                    <v-avatar width="120" height="120">
                        <v-img style="border-radius: 120px"
                               :src="base64 ? base64 : (  auth.image ? $server + auth.image
                       :'https://www.w3schools.com/howto/img_avatar.png' ) "></v-img>
                    </v-avatar>
                    <v-col
                            cols="10" xs="10" sm="10" md="8" lg="8"
                            class="mx-auto"
                    >
                        <v-form @submit.prevent="updateProfile">
                            <div class="file-input">
                                <label class="v-btn info" for="file">
                                    <i class="fas fa-upload fa-lg mr-2"></i>
                                    ເລືອກຮູບໂປຣຟຣາຍ</label>
                                <input @change="imgUpload" type="file" id="file">
                            </div>
                            <v-text-field
                                    v-model="auth.username"
                                    label="username"
                                    outlined
                                    disabled
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.name"
                                    label="ຊື່"
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.lastname"
                                    label="ນາມສະກຸນ"
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.email"
                                    label="ອີເມວ"
                                    disabled
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.phone"
                                    label="ເບິໍໂທລະສັບ"
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.facebookAccount"
                                    label="facebook"
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.whatsapp"
                                    label="whatsapp"
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.skill"
                                    label="ຄວາມສາມາດ"
                                    outlined
                            ></v-text-field>
                            <v-text-field
                                    v-model="auth.history"
                                    label="ປະຫວັດການເຮັດວຽກ"
                                    outlined
                            ></v-text-field>
                            <v-btn type="submit" class="success"> ອັບເດດໂປຣຟຣາຍ </v-btn>
                        </v-form>
                    </v-col>
                </v-card>
            </v-flex>
        </v-layout>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    export default {
        data: () => ({
            base64: '',
            file: '',
            items: [
                {
                    text: 'Dashboard',
                    to: { name: 'Dashboard' },
                    exact: true
                },
                {
                    text: 'Profile',
                }
            ]
        }),
        computed: {
          ...mapGetters([
              'auth'
          ])
        },
        methods: {
            ...mapActions([
               'fetchAuth'
            ]),
            imgUpload(e) {
                const file = e.target.files[0]
                const reader = new FileReader() // Only read base64 not FormData
                if(file['size'] < 2000000) {
                    reader.onloadend = e => {
                        this.base64 = e.target.result
                    }
                    reader.readAsDataURL(file) // Base64 Reader
                    this.file = file // FormData Image
                }else {
                    this.$swal(
                        'Oops...',
                        'You are upload a large file',
                        'error'
                    )
                }
            },
             async updateProfile () {
                 try {
                     const user = this.auth
                     const userdata = new FormData()
                     for(let key in user) {
                         userdata.append(key, user[key])
                     }
                     userdata.append('image', this.file)
                     await this.$axios.put('profile/update',  userdata )
                     await this.fetchAuth()
                     this.$router.push({ name: 'Dashboard'})
                     this.$swal({
                         title: 'ອັບເດດໂປຣໄຟຣແລ້ວ',
                         type: 'success',
                         toast: true,
                         position: 'top',
                         showConfirmButton: false,
                         timer: 2000,
                     })
                 } catch (e) {
                     throw new Error(e)
                 }
            },
        }

    }
</script>

<style lang="scss" scoped>
    .file-input {
        width: 200px;
        margin: auto;
        height: 68px;
        position: relative;
        label, input {
            background: #fff;
            color: #2196F3;
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            padding: 10px;
            border-radius: 4px;
            margin-top: 7px;
            cursor: pointer;
        }
        input {
            opacity: 0;
            z-index: -12;
        }
    }
</style>
